var Cell, GAME_OVER, GAME_OVER_STATES, GRID, ITEMS, Immutable, SNAKE, Snake, XY;

Snake = require('../snake');

GRID = require('../settings').GRID;

Cell = require('../views/cell');

XY = require('../utility/xy');

Immutable = require('immutable');

GAME_OVER_STATES = {
  failure: {
    success: false
  },
  success: {
    success: true
  }
};

SNAKE = null;

ITEMS = 0;

GAME_OVER = null;

module.exports = Object.create(null, {

  /*
    Object that manages the state of the current game.
  
    It doesn't handle Actions, it exposes convenience methods for
    manipulating and reading game state, and lives outside the context
    of Actions.
  
    This isn't a Flux Store; it's just a helper class.
   */
  reset: {
    enumerable: true,
    value: function() {
      SNAKE = Snake.at_position(XY.random(GRID.dimension - 1));
      GAME_OVER = null;
      return ITEMS = 0;
    }
  },
  collision: {
    value: function(xy) {
      return SNAKE.meets(xy);
    }
  },
  collide: {
    value: function(target, xy) {
      if (target === Cell.WALL) {
        return this._fail();
      } else if (target === Cell.SNAKE) {
        if (SNAKE.head() === xy) {
          return this._fail();
        }
      } else if (target === Cell.ITEM) {
        return this._score();
      }
    }
  },
  set_motion: {
    value: function(motion) {
      return SNAKE.set_motion(motion);
    }
  },
  move_snake: {
    enumerable: true,
    value: function() {
      return SNAKE.move();
    }
  },
  out_of_bounds: {
    enumerable: true,
    value: function() {
      var ref, x, y;
      ref = SNAKE.head(), x = ref.x, y = ref.y;
      return Math.min(x, y) < 0 || Math.max(x, y) >= GRID.dimension;
    }
  },
  add_item: {
    enumerable: true,
    value: function() {
      return ITEMS += 1;
    }
  },
  items_left: {
    enumerable: true,
    value: function() {
      return ITEMS;
    }
  },
  over: {
    enumerable: true,
    value: function() {
      return GAME_OVER != null;
    }
  },
  failed: {
    enumerable: true,
    value: function() {
      if (GAME_OVER != null) {
        return !GAME_OVER.success;
      } else {
        return false;
      }
    }
  },
  _score: {
    value: function() {
      SNAKE.grow();
      ITEMS -= 1;
      return this._maybe_win();
    }
  },
  _maybe_win: {
    value: function() {
      if (ITEMS === 0) {
        return GAME_OVER = GAME_OVER_STATES.success;
      }
    }
  },
  _fail: {
    value: function() {
      SNAKE.set_motion(null);
      return GAME_OVER = GAME_OVER_STATES.failure;
    }
  }
});
